import React, { Component, PropTypes } from 'react';
// import pureRender from 'pure-render-decorator';
import { History, Link } from 'react-router';
// import { connect } from 'react-redux';
import { is, fromJS } from 'immutable';

import { Header, Template } from './Common/Mixin';

import  '../style/main';


class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    // state
    componentWillMount() {

    }
    // DOMthis.getDOMNode()
    // DOMDOM
    componentDidMount() {
        const url = window.location.href.split('#')[0];
        const successFun = (res) => {
            wx.config({
                debug: false, // ,apialertpclogpc
                appId: res.appId, // 
                timestamp: res.timestamp, // 
                nonceStr: res.nonceStr, // 
                signature: res.signature, // 1
                jsApiList: ['chooseImage', 'uploadImage'] // JSJS2
            });
        }
        this.props.getData('/activity/getWeiXinInfo.do', { url: url }, successFun, 'jssdk');
        wx.ready(() => {
            wx.hideOptionMenu();
        })
    }
    // propsstatefalsebug
    // 
    shouldComponentUpdate(nextProps, nextState) {
        return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
    }

    //propsstatepropsstate
    componentWillUpdate(nextProps, nextState) {
        if (this.props !== nextProps) {
            let {data} = nextProps.state;
        }
    }

    render() {
        return (
            <div className="index_module">
                <Header nav saleRecord title='' />
            </div>
        )
    }
    // componentDidMount
    // 
    componentWillUnmount() {
        cancelAnimationFrame(this.state.requestID);
    }
}

export default Template({
    id: 'index',  //redux
    component: Index,//
    url: '/api/appHomePage.do',
    data:{a:122}
});




